.reviewList {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.reviewItem {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 8px 4px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.reviewInfo {
  text-align: center;
}

.reviewNotFound {
  text-align: center;
}
